  height: 20px; }

.all {
  width: 20px;
  height: 20px; }

.resources {
  width: 22px;
  height: 18px; }

.friends {
  width: 22px;
  height: 20px; }

.chat {
  width: 20px;
  height: 17.5px; }

.settings {
  width: 22px;
  height: 22px; }

.active-nav {
  border-right: 3px solid #5E81F4; }

.upgrade {
  background: #F5F5FB;
