  line-height: 1.4; }

.upgrade .btn {
  width: 100%; }

.upgrade_m {
  margin: 110px 40px 0; }

.page {
  display: flex; }

.center {
  width: 100%;
  padding: 50px;
  background: #F5F5FB; }

.center__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 47px; }

.center__search-form {
  max-width: 472px;
  width: 100%;
  position: relative; }

.center__search-icon {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 17px;
  left: 23px;
  fill: #8DA1B5; }

.center__search {
  width: 100%;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-right: 23px;
  padding-left: 53px;
  color: #8DA1B5;
