.center__alert {
  width: 20px;
  height: 25px; }

.center__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px; }

.center__info-header {
  font-family: 'inter-semibold', sans-serif;
  font-size: 28px;
  line-height: 1.2; }

.center__nav-item {
  text-decoration: none;
  color: #8DA1B5;
  font-family: 'inter-semibold', sans-serif;
  font-size: 17px;
  line-height: 1.2;
  position: relative;
  margin-left: 50px;
  transition: all .3s ease; }
  .center__nav-item:hover {
    color: #5E81F4; }
    .center__nav-item:hover::after {
      position: absolute;
      background: #5E81F4;
      content: '';
      width: 18px;
      height: 2px;
      border-radius: 2px;
